<template>
  <div class="update-birthday">
    <van-datetime-picker
        v-model="currentDate"
        type="date"
        title=""
        :min-date="minDate"
        :max-date="maxDate"
        @cancel="$emit('close')"
        @confirm="onConfirm"
    />
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user'
import dayjs from 'dayjs'
export default {
    data() {
        return {
            minDate: new Date(1988, 0, 1),
            maxDate: new Date(),
            currentDate: new Date(this.value),
        }
    },
    methods: {
        async onConfirm() {
            // 
            // 
            // const date = `${this.currentDate.getFullYear()}-${this.currentDate.getMonth() + 1}-${this.currentDate.getDate()}`
            const date = dayjs(this.currentDate).format('YYYY-MM-DD')
            this.$toast.loading({
                message:'',
                forbidClick: true // 
            })
            await updateUserProfile({
                birthday: date
            })
            this.$emit('close') // 
            this.$emit('input', date) // 
            this.$toast.success({
                message:'',
                forbidClick: true // 
            })
        }
    },
    props:{
        value: {
            type: String,
            required: true
        }
    }
}
</script>

