<template>
  <div class="update_name">
      <van-nav-bar
        left-text=""
        right-text=""
        @click-left="$emit('close')"
        @click-right="onConfirm"
        title=""
      >
      </van-nav-bar>
      <div style="padding:10px">
        <van-field
            v-model="localName"
            rows="2"
            autosize
            type="textarea"
            maxlength="10"
            placeholder=""
            show-word-limit
        />
      </div>
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user'
export default {
    name: 'updateName',
    components: {

    },
    data() {
        return {
            localName: this.value
        }
    },
    methods: {
        async onConfirm() {
            console.log('');
            //  
            // name
            this.$toast.loading({
                message:'',
                forbidClick: true // 
            })
            try { // try
                await updateUserProfile({
                    name: this.localName
                })
                this.$emit('close') // 
                this.$emit('input', this.localName) // 
                this.$toast.success({
                    message:'',
                    forbidClick: true // 
                })
            } catch (error) { // catch
                if(error && error.response && error.response.status === 409) {
                    this.$toast.fail('')
                }
            }
            
        }
    },
    props: {
        // namev-model="name"
        // localName
        value: {
            type: String,
            required: true
        }
    }
}
</script>

