<template>
   <div class="user-profile">
       <van-nav-bar
            class="app-nav-bar"
            title=""
            left-arrow
            @click-left="$router.back()"
        />
        <input 
            type="file"
            hidden 
            ref="file"
            accept="image/*"
            @change="onFileChange"
        >
        <!-- / -->
        <van-cell title="" is-link @click="$refs.file.click()">
            <van-image
            width="30"
            height="30"
            round
            fit="cover"
            :src="user.photo"
            />
        </van-cell>
        <van-cell 
            title="" 
            is-link
            :value="user.name"
            @click="isEditNameShow = true"
        ></van-cell>
        <van-cell 
            title="" 
            is-link
            :value="user.gender === 0 ? '' : ''"
            @click="isEditGenderShow = true"
        ></van-cell>
        <van-cell 
            title="" 
            is-link
            :value="user.birthday"
            @click="isEditBirthdayShow = true"
        ></van-cell>
        <van-popup
            v-model="isEditNameShow"
            position="bottom"
            :style="{height: '60%'}"
        >
            <!-- name
                v-model
                 -->
            <!-- @close="isEditNameShow= false"
              :name="user.name"
              @update-name="user.name = $event" -->
            <!-- v-model
                v-model="user.name"
                value,value
                :value="user.name"
                input   @input="user.name = $event"
                $emit('input',xxxx)  -->
            <!-- syncsync
                sync
                :gender.sync = "user.gender"
                ===>
                @update:gender="user.gender = $event" <==> @update:="user.gender = $event"
                :gender = "user.gender"
                
                this.$emit('update:gender', xxx) 
                gender
             -->
            <update-name 
              v-if="isEditNameShow"
              :gender.sync = "user.gender"
              v-model="user.name"
              @close="isEditNameShow= false"
            />
        </van-popup>
        <!--  -->
        <van-popup
            v-model="isEditGenderShow"
            position="bottom"
            :style="{ height: '30%'}"
        >
            <update-gender
                v-if="isEditGenderShow"
                v-model="user.gender"
                @close="isEditGenderShow = false"
            ></update-gender>
        </van-popup>
        <!--  -->
        <van-popup
            v-model="isEditBirthdayShow"
            position="bottom"
            :style="{ height: '50%'}"
        >
            <update-birthday
                v-if="isEditBirthdayShow"
                v-model="user.birthday"
                @close="isEditBirthdayShow = false"
            ></update-birthday>
        </van-popup>
        <!--  -->
        <van-popup
            v-model="isEditPhotoShow"
            class="photo-update"
            position="bottom"
            :style="{ height: '100%'}"
        >
            <update-photo
                v-if="isEditPhotoShow"
                :file="previewImage"
                @close="isEditPhotoShow = false"
                @update-photo="user.photo = $event"
            ></update-photo>
        </van-popup>
   </div>
</template>

<script>
import {getUserProfile} from '@/api/user'
import UpdateName from './component/update-name'
import UpdateGender from './component/update-gender'
import UpdateBirthday from './component/update-birthday'
import UpdatePhoto from './component/update-photo'
export default {
    name: 'UserProfile',
    data() {
        return {
            user: {}, // 
            isEditNameShow: false, // 
            isEditGenderShow: false, // 
            isEditBirthdayShow: false, // 
            isEditPhotoShow: false, // 
            previewImage: null // 
        }
    },
    components: {
        UpdateName,
        UpdateGender,
        UpdateBirthday,
        UpdatePhoto
    },
    created() {
        this.loadUserProfile()
    },
    methods: {
        async loadUserProfile() {
            // 
            const { data } = await getUserProfile()
            // console.log(data);
            this.user = data.data
        },
        onFileChange() {
            // inputfileInput
            // 1.   2. 
            // 
            // 
            const file = this.$refs.file.files[0]
            // URL
            // console.log(file)
            this.previewImage = file
            this.$refs.file.value = ''
            this.isEditPhotoShow = true
        }
    }
}
</script>

<style scoped lang="less">
.van-popup{
    background-color: #f5f7f9!important;
}
.photo-update {
    background: #000!important;
}
</style>